import React from 'react';
import { makeStyles , withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Avatar from '@material-ui/core/Avatar';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import HomeRoundedIcon from '@material-ui/icons/HomeRounded';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import SendRoundedIcon from '@material-ui/icons/SendRounded';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Badge from '@material-ui/core/Badge';
import avatarimg from "../images/avatar.jpg"

const StyledBadge = withStyles((theme) => ({
    badge: {
      backgroundColor: '#44b700',
      color: '#44b700',
      boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
      '&::after': {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        borderRadius: '50%',
        animation: '$ripple 1.2s infinite ease-in-out',
        border: '1px solid currentColor',
        content: '""',
      },
    },
    '@keyframes ripple': {
      '0%': {
        transform: 'scale(.8)',
        opacity: 1,
      },
      '100%': {
        transform: 'scale(2.4)',
        opacity: 0,
      },
    },
  }))(Badge);
  
  const SmallAvatar = withStyles((theme) => ({
    root: {
      width: 22,
      height: 22,
      border: `2px solid ${theme.palette.background.paper}`,
    },
  }))(Avatar);


const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      maxWidth: 360,
      backgroundColor:"#F9F9F9",
      height:"93vh",
      position:" fixed",
      bottom: "0%",
      left: "0",
      display: "flex",
      flexDirection: "column",
      justifyContent: "space-between"
    },
    social:{
        textAlign: "center",
        marginTop: "10px",
        fontSize:"18px",
        fontWeight: "600"
    },
    sizeIcon:{
        width:"40px",
        height:"40px",
        color:"black",
        backgroundColor:theme.palette.background.paper,
        
    },
    iconDefault:{
        fontSize:"20px",
        fontWidth:"700",
        "&:span":{
            fontSize:"50px"
        }
    },
    sizeList:{
        minWidth: "31px",/* From https://css.glass */
        
    },
    LogoutSize:{
        minWidth: "31px",
    },
    AvatarImage:{
        display: 'flex',
        justifyContent:" center",
        marginTop:"10px",
    '& > *': {
      margin: theme.spacing(1),
    },
    },
    sizeImage:{
        width:"80px",
        height:"80px",
        boxShadow:"  0px 0px 2px 4px #fff , 0px 0px 1px 6px #d81b60",
    },
    spanSize:{
        fontSize: "16px",
        fontFamily: "'Poppins'",
        fontWeight: 600,
        
    },
    IconDivider:{
        fontSize:"24px",
        color:"#454546",

    }
  }));


const DividerUi = () => {
  

    const classes = useStyles();
    return (
        <>
        
            <List component="nav" className={classes.root} aria-label="mailbox folders">
            <div>
            <div className={classes.AvatarImage}>
            <StyledBadge 
                overlap="circular"
                anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'right',
                }}
                variant="dot"
            >
                <Avatar className={classes.sizeImage} alt="Remy Sharp" src={avatarimg} />
            </StyledBadge>
            </div>
            <p className={classes.social}>{}</p>
            <ListItem button>
                <ListItemAvatar className={classes.sizeList}>
                        <HomeRoundedIcon className={classes.IconDivider} />
                </ListItemAvatar>
                    <span className={classes.spanSize}>HOME</span>
            </ListItem>
            <ListItem button className={classes.sizeList}>
            <ListItemAvatar className={classes.sizeList}>
                            <SendRoundedIcon className={classes.IconDivider} />
                </ListItemAvatar>
                    <span className={classes.spanSize} >SEND</span>
            </ListItem>
            </div>
            <div>
            <ListItem button  className={classes.LogoutSize}>
            <ListItemAvatar className={classes.sizeList}>
                            <ExitToAppIcon />
                </ListItemAvatar>
                <span className={classes.spanSize}>Logout</span>
            </ListItem>
            </div>
            </List>
        </>
    );
}

export default DividerUi;

